    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?u64pwc') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?u64pwc') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?u64pwc##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

